.card {
  width: 17rem;
  position: absolute;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
  text-align: center;
  background: rgba(255, 255, 255, 0.26);
  border: 7px solid var(--orangeCard);
  box-shadow: var(--boxShadow);
  border-radius: 20px;
  padding: 0px 24px 2rem 24px;
}

.card span:nth-of-type(2) {
  color: var(--gray);
  font-size: 1.2rem;
}

.card>img {
  transform: scale(0.6);
  margin-bottom: -2rem;
}

.c-button {
  background: #FFFFFF;
  box-shadow: 0px 19px 60px rgba(0, 0, 0, 0.08);
  border-radius: 7px;
  border: none;
  padding: 10px;
  font-size: 16px;
  color: #5290FD;
}

.c-button:hover {
  transform: scale(1.1);
  cursor: pointer;
}

/* cards */




@media screen and (max-width: 767px) {
  .card {
    width: auto;
  }
}