.footer {
  display: flex;
  align-items: center;
  position: relative;
  margin-top: -7rem;
}

.f-content {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  margin-top: 4rem;
  gap: 4rem;
  color: white;
}

.f-icons {
  display: flex;
  gap: 2rem;
}

@media screen and (max-width: 767px) {
  .f-content {
    transform: scale(.5);
  }

  .footer img {
    height: 250px;
  }
}